const { ccclass, property } = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    numberArray: any[] = [];

    numberCount: number = 10000;

    onLoad () {
        //使用正整数填充 numberArray
        for (let i = 0; i < this.numberCount; i++) {
            this.numberArray[i] = i;
        }
    }

    addIntNumber () {
        for (let i = 0; i < this.numberCount; i++) {
            this.numberArray[i] += 1;
        }
        console.log("add int number");
    }

    addFloatNumber () {
        for (let i = 0; i < this.numberCount; i++) {
            this.numberArray[i] += 1.000000000001;
        }
        console.log("add float number");
    }

    numberJITTest () {
        console.time("numberJITTest");
        for (let index = 0; index < this.numberArray.length; index++) {
            let item = Math.pow(this.numberArray[index], 100);
        }
        console.timeEnd("numberJITTest");
    }
}
